import React, { useCallback, useState } from 'react';
import s from './NewNote.module.scss';
const AddNewNote = ({ addNewNote }) => {
    const [value, setValue] = useState('');
    const [errorMessage, setErrorMessage] = useState(null);
    const changeHandler = (e) => {
        if (errorMessage) {
            setErrorMessage(null);
        }
        setValue(e.target.value);
    };
    const addItem = useCallback(() => {
        if (value.trim() !== '') {
            setErrorMessage(null);
            addNewNote(value);
            setValue('');
        }
        else {
            setErrorMessage('Please enter the title');
        }
    }, [addNewNote, value]);
    return (React.createElement("div", { className: s.NewNote },
        React.createElement("input", { type: "text", value: value, onChange: changeHandler, className: "input", placeholder: "Add Note Title" }),
        React.createElement("button", { className: s.ButtonSubmit, onClick: addItem }, "addNewNote")));
};
export default AddNewNote;
