import TextField from '@material-ui/core/TextField';
import React, { useState } from 'react';
import s from './TitleField.module.scss';
export const TitleField = (props) => {
    const [title, setTitle] = useState(props.value);
    const [error, setError] = useState(null);
    const [editMode, setEditMode] = useState(false);
    const clickHandler = () => {
        setEditMode(true);
    };
    const activateViewMode = () => {
        if (title.trim() !== '') {
            setEditMode(false);
            props.onChange(title);
        }
        else
            setError('Please enter Title');
    };
    const changeTitle = (e) => {
        if (error)
            setError(null);
        setTitle(e.currentTarget.value);
    };
    return editMode ? (React.createElement(TextField, { className: s.NoteItemTitle, value: title, onChange: changeTitle, autoFocus: true, error: !!error, helperText: error, onBlur: activateViewMode })) : (React.createElement("span", { onClick: clickHandler, className: s.NoteItemTitle }, props.value));
};
