import React, { useCallback, useState } from 'react';
import s from './TextField.module.scss';
export const TextField = (props) => {
    const [value, setValue] = useState(props.text);
    const [editMode, setEditMode] = useState(false);
    // const [markedupText, setMarkedUpText] = useState<string>('');
    const onChangeHandler = (e) => {
        if (e.target.value.length <= 640) {
            setValue(e.currentTarget.value);
        }
    };
    const activeViewMode = () => {
        if (value.trim() !== '') {
            setEditMode(false);
            getNewTag();
        }
        else {
            setValue('Enter some text');
        }
    };
    const getNewTag = useCallback(() => {
        const hashTagIndex = value.indexOf('#');
        if (hashTagIndex === -1) {
            props.addTag('#all');
            props.changeText(value, props.id, '#all');
        }
        else {
            let result = '';
            for (let i = hashTagIndex; i < value.length; i++) {
                if (value[i] !== ' ') {
                    result = result + value[i];
                }
                else {
                    break;
                }
            }
            props.addTag(result);
            props.changeText(value, props.id, result);
        }
    }, [props.changeText, props.addTag, props.id, value]);
    const clickHandler = () => {
        setEditMode(true);
        // let MarkedText = props.tag;
        // let editValue = value.replace(MarkedText, '<mark>' + MarkedText + '</mark>');
    };
    return editMode ? (React.createElement("div", null,
        React.createElement("textarea", { cols: 25, rows: 5, className: s.Textarea, value: value, onChange: onChangeHandler, onBlur: activeViewMode }))) : (React.createElement("div", { onClick: clickHandler, className: s.NoteTextWrapper },
        React.createElement("p", { className: s.TextContent }, value)));
};
